@import "vendor/uikit-2.23.0/core/navbar.less";
@import "vendor/uikit-2.23.0/core/subnav.less";
@import "vendor/uikit-2.23.0/core/breadcrumb.less";
@import "vendor/uikit-2.23.0/core/pagination.less";

// Elements
@import "vendor/uikit-2.23.0/core/list.less";
@import "vendor/uikit-2.23.0/core/table.less";
@import "vendor/uikit-2.23.0/core/form.less";

// Common
@import "vendor/uikit-2.23.0/core/button.less";
@import "vendor/uikit-2.23.0/core/icon.less";
@import "vendor/uikit-2.23.0/core/close.less";
@import "vendor/uikit-2.23.0/core/badge.less";
@import "vendor/uikit-2.23.0/core/alert.less";
@import "vendor/uikit-2.23.0/core/thumbnail.less";
@import "vendor/uikit-2.23.0/core/overlay.less";
